.markdown {
  color: $primary-text-color;
  font-family: "Source Sans Pro","Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 17px;
  line-height: 1.6em;
  word-wrap: break-word;

  section {
    margin-bottom: 16px;
    padding: 16px 0;
  }

  h2 {
    color: $accent-color;
    font-size: 24px;
    line-height: 26px;
    border-bottom: 1px solid rgba(0,0,0,.12);
    padding-bottom: .3em;
    margin-bottom: 16px;
  }

  h3 {
    font-size: 20px;
    line-height: 22px;
    font-weight: 600;
    margin-top: 16px;
    margin-bottom: 16px;
  }

  h4 {
    color: $primary-color;
    font-size: 18px;
    line-height: 20px;
    font-weight: normal;
    margin-top: 12px;
    margin-bottom: 12px;
  }

  h5 {
    font-size: 18px;
    line-height: 18px;
    font-weight: normal;
    font-style: italic;
    margin-top: 8px;
    margin-bottom: 8px;
  }

  p {
    margin-top: 0;
    margin-bottom: 16px;
  }

  blockquote {
    background: #f9f9f9;
    border-left: 10px solid #ccc;
    margin: 1.5em 10px;
    padding: 0.5em 10px;
    quotes: "\201C""\201D""\2018""\2019";
  }

  blockquote:before {
    color: #ccc;
    content: open-quote;
    font-size: 4em;
    line-height: 0.1em;
    margin-right: 0.1em;
    vertical-align: -0.4em;
  }

  blockquote:after {
    color: #ccc;
    content: close-quote;
    font-size: 4em;
    line-height: 0.1em;
    margin-left: 0.1em;
    vertical-align: -0.4em;
  }

  blockquote p {
    display: inline;
  }

  code {
    color: #0086b3;
    padding: .2em .4em;
    margin: 0;
    font-size: 85%;
    background-color: rgba(0,0,0,.04);
    border-radius: 3px;
    font-family: 'Source Code Pro';
  }

  a {
    color: $accent-color;
  }

  img {
    max-width: 100%;
    box-sizing: content-box;
    background-color: $white;
    border-radius: 4px;
    border: 6pt solid $white;
    overflow: hidden;
    display: block;
    margin: 0 auto;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
  }

  strong {
    font-weight: 700;
  }

  ul {
    padding: 0 0 0 2em;
    list-style-type: disc;
    margin-bottom: 16px;

    li {
      display: list-item;
    }
  }

  ol {
    padding: 0 0 0 2em;
    list-style-type: decimal;
    margin-bottom: 16px;

    li {
      display: list-item;
    }
  }

  pre {
    padding: 16px;
    overflow: auto;
    font-size: 85%;
    line-height: 1.45;
    background-color: #f7f7f7;
    border-radius: 3px;
    margin-top: 0;
    margin-bottom: 16px;
    word-wrap: normal;
  }

  pre code {
    display: inline;
    max-width: initial;
    padding: 0;
    margin: 0;
    overflow: initial;
    line-height: inherit;
    word-wrap: normal;
    background-color: transparent;
    border: 0;
  }

  .info, .warning, .danger {
    padding: 16px 20px;
    font-size: 18px;
    box-shadow: $box-shadow;
    border-radius: $border-radius;
    margin: 48px 0 24px 0;
    text-align: center;

    .icon {
      border-radius: 100%;
      width: 45px;
      height: 45px;
      margin: -40px 0 12px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: -42px auto 8px;

      i {
        width: 25px;
        height: 25px;
        background-repeat: no-repeat;
      }
    }

    h4 {
      font-size: 20px;
      margin-bottom: 16px;
    }

    p {
      font-style: italic;
      font-weight: 200;
      line-height: 23px;
    }
  }

  .info {
    background: #F0EDF5;
    border: 2px solid #BDA9DA;

    .icon {
      background: #FFF;
      border: 2px solid #BDA9DA;

      i {
        background-image: url('./images/alert/info.svg');
      }
    }

    h4 {
      color: #70519E;
    }
  }

  .warning {
    background: #FCF9E4;
    border: 2px solid #E6D55B;

    .icon {
      background: #FFF;
      border: 2px solid #E6D55B;

      i {
        background-image: url('./images/alert/warning.svg');
      }
    }

    h4 {
      color: #B9810B;
    }
  }

  .danger {
    background: #F9E9E8;
    border: 2px solid #DFA29F;

    .icon {
      background: #FFF;
      border: 2px solid #DFA29F;

      i {
        background-image: url('./images/alert/danger.svg');
      }
    }

    h4 {
      color: #A31F07;
    }
  }

  table {
    display: block;
    width: 100%;
    overflow: auto;
    word-break: normal;
    word-break: keep-all;
  }

  table th {
    font-weight: bold;
    text-align: left;
  }

  table th,
  table td {
    padding: 6px 13px;
    border: 1px solid #ddd;
  }

  table tr {
    background-color: #fff;
    border-top: 1px solid #ccc;
  }

  table tr:nth-child(2n) {
    background-color: #f8f8f8;
  }

  code-listing {
    background-color: #fff;
    display: block;
    border-radius: $border-radius;
    margin-top: 16px;
    margin-bottom: 16px;
    border: 1px solid rgba(0,0,0,.12);
    box-shadow: $box-shadow;

    .title-bar {
      padding: 0;
      z-index: 1;
      border-bottom: 1px solid rgba(0,0,0,.12);
      display: flex;
      
      h4 {
        font-size: 18px;
        color: #5B5B5B;
        line-height: 40px;
        margin: 0 16px;
        flex: 1;
      }

      .src-lang {
        display: flex;
        align-items: center;
        border-left: 1px solid rgba(0,0,0,.12);
        padding: 0 16px;
      }

      drop-down {
        display: inline-block;

        > .container {
          position: relative;
          height: 100%;

          > .button {
            display: inline-block;
            border-left: 1px solid rgba(0,0,0,.12);
            padding: 6px 16px;
            height: 100%;
            cursor: pointer;

            & > .selected-item-container {
              display: inline-block;
              margin-right: 16px;
            }

            &::after {
              content: '▾';
              display: inline-block;
              color: black;
              position: absolute;
              right: 8px;
            }

            &.open::after {
              top: 8px;
              transform: rotate(180deg);
            }
          }

          > .options-container {
            position: relative;
            left: 1px;

            > .options {
              position: absolute;
              top: 1px;
              right: 0px;
              border: 1px solid rgba(0,0,0,.12);
              border-top: 0;
              box-shadow: $box-shadow;
              background: $white;
              z-index: 10000;
              list-style-type: none;
              padding: 0;

              > li {
                cursor: pointer;
                padding: 6px 12px;

                &:hover {
                  background: #f6f6f6;
                }
              }
            }
          }
        }
      }
    }

    .code-container {
      pre {
        background-color: #FCFCFC;
        margin: 0;
        padding: 0;

        code {
          background-color: #FCFCFC;
        }
      }
    }
  }

  $primary-dark-color:   #70509D;
  $primary-light-color:  #9A84BA;
  $primary-accent-color: #A1478C;

  au-demo {
    background-color: $primary-light-color;
    color: white;
    font-size: 1em;
    display: block;
    position: relative;
    margin-top: 2rem;
    margin-bottom: 2rem;
    padding: 20px;
    min-width: 100%;
    border-radius: $border-radius;
    border: 2px solid $primary-dark-color;
    box-shadow: 0 10px 40px -10px $primary-color;

    > h4:first-child {
      margin: -20px -20px 15px;
      padding: 10px 0;
      text-align: center;
      background-color: $primary-dark-color;
      color: white;
    }

    > h2, > h3, > h4, > h5, > h6 {
      font-weight: 400 !important;
      color: rgba(255,255,255,0.8) !important;
      border-bottom: 1px solid rgba(255,255,255,0.2);
    }

    p {
      color: $white; 
      font-weight: 600;
    }

    label {
      font-size: 0.9em;
      margin-bottom: -5px;
      color: rgba(255,255,255,0.8);
    }

    input.au-target,
    select.au-target {
      color: #222;
      margin: 5px 0;
    }

    input[type="text"],
    input[type="color"] {
      padding: 5px 10px;
      border-radius: $border-radius;
      border: 1px solid $primary-color;
      box-shadow: inset 0 1px 2px 0 rgba(0,0,0,0.4), 0 1px 0 0 rgba(255,255,255,0.2);
    }

    //Note: Safari doesn't render this input type as a button. It renders as a text input.
    input[type="color"] {
      padding: 0;
    }

    button {
      padding: 3px 10px 5px;
      border-radius: $border-radius;
      background-color: $accent-color;
      color: $white;
      border: 1px solid $primary-accent-color;
      box-shadow: 0 1px 0 0 rgba(0,0,0,0.2), inset 0 1px 0 0 rgba(255,255,255,0.2);
    }

    button:active {
      box-shadow: inset 0 1px 2px 0 rgba(0,0,0,0.4);
    }

    table.table {
      color: #222;
    }

    a.au-target {
      color: $white !important;
    }

    output {
      background-color: rgb(247, 247, 247);
      border-radius: $border-radius;
      padding: 16px;
      display: block;
      box-shadow: 0 1px 0 0 rgba(0,0,0,0.2), inset 0 0 10px 0 rgba(154,132,186,0.2);
    }
  }
}

@media only screen and (max-width : $sidebar-max-width) {
  .markdown {
    section {
      code-listing {
        .title-bar {
          display: block;
      
          .src-lang {
            border-top: 1px solid rgba(0,0,0,.12);
            border-left: none;
            line-height: 40px;
          }
      
          drop-down {
            display: block;

            > .container {
              > .button {
                border-top: 1px solid rgba(0,0,0,.12);
                border-left: none;
                width: 100%;
              }
            }
          }
        }
      }
    }
  }
}
